import { Component, OnInit } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { CriarItemService } from '../services/criacoes/criar-item.service';
import { ItemService } from '../services/criacoes/item/item.service';
import { promise } from 'protractor';
import { PaginacaoService } from '../services/utils/paginacao.service';
import { ItemUpdateComponent } from '../components/criacoes/item-update/item-update.component';

@Component({
  selector: 'app-gerequipamentos',
  templateUrl: './gerequipamentos.page.html',
  styleUrls: ['./gerequipamentos.page.scss'],
})
export class GerequipamentosPage implements OnInit {

  tipoItem: number = -1;
  display: boolean = false;
  listDisplay: boolean = false

  page;

  paginaAtual = 0;

  constructor(public modalCtrl: ModalController,
    private itemService: ItemService,
    public alertController: AlertController,
    private paginaService: PaginacaoService) { }

  ngOnInit() {
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    let p;
    
    switch (Number(this.tipoItem)) {
      case 0:
        p = this.itemService.findPageOfAtaque(this.paginaAtual.toString());
        this.paginaService.setPromisse(p);
        break;

      case 1:
        p = this.itemService.findPageOfDefesa(this.paginaAtual.toString());
        this.paginaService.setPromisse(p);
        break;

      case 2:
        p = this.itemService.findPageOfUtilizavel(this.paginaAtual.toString());
        this.paginaService.setPromisse(p);
        break;

      default:
        console.log('NADA');
        break;
    }
    this.listar();
  }

  listar() {  
    this.listDisplay = true;
    this.display = false;
  }

  destroyList(){
    this.paginaAtual = 0 ;
    this.listDisplay = false;
  }

  novo() {
    this.listDisplay = false;
    if (this.display) {
      this.display = false;
    } else {
      this.display = true;
    }
  }

  async selectedObj(event) {
    this.itemService.setStaticID(event.id)
    this.itemService.setStaticType(Number(this.tipoItem));
    const modal = await this.modalCtrl.create({
      component: ItemUpdateComponent,
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',
      id: 'princi'
    });

    modal.onDidDismiss()
      .then(res => {
        const { data } = res;
        //console.log(res);
        if (data == true) {
          this.consultar();
          this.paginaService.requestRefresh(true);
        }
      });

    return await modal.present();
  }


}

